button {
  background: rgb(233,255,52);
  font-size: 16px;
  font-weight: bold;
  padding: 1px;
  padding-left: 10px;
  padding-right: 10px;
  height: 1.8em;
  border-radius: 0;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(166, 166, 166);
  float: right;
  -webkit-appearance: none;
}




