.timer-input {
  display: inline-block;
  width: 50%;
  margin: 0.5em;
  height: 50px;
}

input {
  font-size: 16px;
  width: 100%;
  border-radius: 0;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(166, 166, 166);
  -webkit-appearance: none;
}




